* {
  padding: 0;
  margin: 0;
  
}
.App {

     text-align: center;
   
    
}
 

.header-color {
    background: #000000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #000000, #000000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #000000, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.left-header {
    background: #000000;  /* fallback for old browsers */
    background: -webkit-linear-gradient(to top, #000000, #000000);  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #000000, #000000); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}
.mdl-layout__header-row .mdl-navigation__link {
    font-size: 15px;
    color: white;
}

.mdl-layout__drawer .mdl-navigation .mdl-navigation__link:hover {
   background-color: transparent !important;
   
}
.mdl-layout__header-row .mdl-navigation__link:hover {
    background: rgb(202, 12, 12);
    cursor: pointer;
}
li a {
    color: white;
    font-weight: 500;
    text-decoration: none;
    display: flex;
    justify-content: right;
    line-height: 3rem;
}
li a:hover {
    background: rgb(202, 12, 12);
}
.homepage {
    height: 90vh;
    background-color: black;
    background-image: linear-gradient(rgba(0,0,0,0.5), rgba(0,0,0,0.5));
}

h2 {
  font-size: 45px;
  color: white;
  text-shadow: 2px 2px 42px #ff3737;
  font-family: 'Staatliches', cursive;
  font-weight: bold;
  line-height: 48px;
  margin-top: 24px;
  margin-bottom: 24px;
  
}

.avatar-img {
  display: flex;
    width: 83%;
    left: 0;
    justify-content: center;
    top: 0;
    position: relative;
    margin: 40px;
}

.banner-text {
  opacity: .8;
  width: 80%;
  margin: auto 45px;
  left: 0;
  top: 45%;
  position: absolute;
}

.banner-text h1 {
  font-size: 30px;
  font-family: 'Jomolhari', serif;
  color: white;
  text-shadow: 2px 2px 42px #ff3737;
}


.banner-text hr {
  border-top: 5px dotted white;
  width: 40%;
  margin: auto;
}
.about-right-col {
  color: white;
  
 
}
.about-left-col {
  padding: 4rem;
  
}
hr {
  width: 100%;
}
h5 {
  color: white; 
}
.contact-body {
  width: 100vw;
  height: 90vh;
}
.social-links  {
  font-size: 3.5rem;
  color: white;
  margin: auto;
  padding: 1rem;
}
.social-links a {
  
    color: white;
    font-weight: 500;
}

.user-name span{
  color: rgb(255, 255, 0);
  font-family: 'Jomolhari', serif;
  

}
.status {
  font-size: 20px;
  color: white;
  font-family: 'Roboto Mono', monospace;
  font-weight: bold;

}

.mdl-grid {
  padding: 8px;
  background: black;
}
.mdl-grid {
  flex-flow: row wrap;
  margin: 0 auto 0 auto;
  align-items: stretch;
  justify-content: center;

}
.dataInfo {
  justify-content: center;
}
.contact-list {
  background: white;
}
p{
  color: white;
}
.contact-body p {
  width: 75%;
  margin: auto;
  padding-top: 1em;
  color: white;
}
.mdl-layout__drawer-button {
  display: block;
  position: absolute;
  height: 48px;
  width: 50px;
  border: 0;
  flex-shrink: 0;
  overflow: hidden;
  text-align: center;
  cursor: pointer;
  font-size: 26px;
  line-height: 56px;
  font-family: Helvetica, Arial, sans-serif;
  top: 0;
  left: 0;
  color: red;
  z-index: 4;
}

.fa-facebook-square:before {
  content: "\f082";
  padding: 0.5rem;
}
.fa-github-square:before {
  content: "\f092";
  padding: 0.5rem;
}
.fa-instagram:before {
  content: "\f16d";
  padding: 0.5rem;
}
.fa-twitter:before {
  content: "\f099";
  padding: 0.5rem;
}
.fa-envelope:before {
  content: "\f0e0";
  padding: 0 5px;
}
.contact-body {
  padding:0;
  
}
.material-icons {
  font-family: 'Material Icons';
  font-weight: normal;
  font-style: normal;
  color: red;
  font-size: 24px;
  line-height: 1;
  letter-spacing: normal;
  text-transform: none;
  display: inline-block;
  word-wrap: normal;
  -webkit-font-smoothing: antialiased;
}

@media only screen and (min-width: 992px) {
    .mdl-cell--12-col, .mdl-cell--12-col-desktop.mdl-cell--12-col-desktop {
      width: calc(100% - 16px);
      background-repeat: no-repeat;
      background-position: center;
    background-image: linear-gradient( rgba(0, 0, 0, 10.5), rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)),url('https://i.ibb.co/Krb1ZV6/portrait.png');
    }

    .about-right-col {
      color: white;
      padding: 7rem;
     
    }
    .avatar-img {
      width: 35%;
      left: 7%;
      position: absolute;
    }
    .banner-text {
    opacity: .8;
    width: 80%;
    margin: auto;
    left: 30%;
    top: 8%;
    }
    .banner-text h1 {
      font-size: 60px;

    }
    .status {
    font-size: 27px;

    }
} 


@media only screen and (max-width: 320px) {
    .mdl-cell {
      margin: 8px;
      width: calc(100% - 16px);
    }

    .avatar-img {
    width: 58%;
    top: 0;
    left: 0;
    position: absolute;
    }
    .banner-text {
    opacity: .8;
    width: 75%;
    position: absolute;
    }
    .banner-text h1 {
      font-size: 25px;
      
    }
    .status {
    font-size: 15px;

    }

    .social-links {
    font-size: 2.5rem;
    padding: 1rem;
    }
    .banner-text hr {
    border-top: 5px dotted white;
    width: 95%;
    margin: auto;
    }
} 
